@use 'base/variables' as v;
h1,
h2,
h3 {
    margin: 0;
    line-height: 1.2;
    color: v.$blanco;
}

h1 {
    font-size: 4rem;
    font-family: v.$fuenteHeading;
}

h2 {
    font-size: 3.6rem;
    font-family: v.$fuenteHeading;
}

h3 {
    font-size: 2.5rem;
}

[class$="heading"] {
    margin: 2rem 0;
}

a {
    text-decoration: none;
    color: v.$negro;
}

p {
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 1.8;
}