/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
body.fijarbody {
  overflow: hidden;
}

main {
  flex: 1;
}

.contenedor {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto;
}

.contenedor-max {
  width: 75%;
  max-width: 1000px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
  color: #FFFFFF;
}

h1 {
  font-size: 4rem;
  font-family: "Staatliches", cursive;
}

h2 {
  font-size: 3.6rem;
  font-family: "Staatliches", cursive;
}

h3 {
  font-size: 2.5rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-color: #5185FF;
}
.header__barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__img {
  width: 5rem;
  height: 6rem;
  object-fit: cover;
}
.header__titulo {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2rem;
  margin-left: 1rem;
}

.nav {
  display: none;
}
.nav__a {
  font-size: 1.9rem;
  color: #FFFFFF;
  margin-right: 3rem;
}
.nav__a:last-of-type {
  margin: 0;
}
.nav__a i {
  margin-right: 0.6rem;
}
.nav__a:hover {
  color: #e6e6e6;
}
@media (min-width: 768px) {
  .nav {
    display: block;
  }
}

.menu i {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.contenedor-menu {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 6rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}
.contenedor-menu a {
  text-align: center;
  display: block;
  color: #FFFFFF;
  padding: 2rem;
  background-color: #446fd1;
  margin: 0.5rem 0;
  transition: background-color, color 0.4s ease-in-out;
}
.contenedor-menu a:hover {
  cursor: pointer;
  background-color: #9ebbff;
  font-weight: bold;
}
.contenedor-menu a i {
  margin-right: 0.8rem;
}

.definicion {
  margin: 2rem auto;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .definicion {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: unset;
  }
}
.definicion .momentanea {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: auto auto;
}
@media (min-width: 1024px) {
  .definicion .momentanea {
    display: none;
  }
}
@media (min-width: 1024px) {
  .definicion {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(3, 1fr);
    background-image: url(../img/img4.jpg);
    background-repeat: no-repeat;
    background-position: bottom right;
  }
}
.definicion__contenido {
  padding: 4rem 5rem;
  background-color: #5185FF;
  text-align: justify;
}
@media (min-width: 1024px) {
  .definicion__contenido {
    grid-column: 1/3;
  }
}
.definicion__contenido h2 {
  margin: 0;
  text-align: center;
}
.definicion__contenido p {
  color: #FFFFFF;
}

.novedades {
  margin: 2rem 0;
}
.novedades__titulo {
  text-align: center;
  color: #819ee0;
  margin: 2rem 0;
}
.novedades__grid {
  display: grid;
  grid-template-columns: repeat(3, 33rem);
  column-gap: 5rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.novedades__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .novedades__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    overflow-x: hidden;
  }
}

.novedad__mask {
  overflow: hidden;
}
.novedad__img {
  height: 20rem;
  width: 100%;
  transition: all 0.3s;
}
.novedad__img:hover {
  transform: scale(1.3);
}
.novedad__titulo {
  background-color: #91b2fd;
  padding: 2rem;
  text-align: center;
}
.novedad__contenido p {
  margin: 0;
  text-align: justify;
}
.novedad__enlace {
  font-weight: bold;
  color: #5185FF;
}

.footer {
  background-color: #5185FF;
}
.footer__grid {
  padding: 2rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.redes h3 {
  text-align: center;
}
.redes__iconos {
  margin-top: 1rem;
  font-size: 3rem;
  display: flex;
  justify-content: space-evenly;
}
.redes__iconos a {
  color: #FFFFFF;
}

.copy {
  background-color: #91b2fd;
  padding: 1rem;
}
.copy p {
  text-align: center;
  font-weight: bold;
  margin: 0;
  color: #FFFFFF;
}